import { Component, Input, OnInit, Output, EventEmitter } from '@angular/core';
import { ProductsService } from '../products.service';
import { CartService } from '../cart.service';
import { Observable } from 'rxjs';
import { Product } from '../models/product';
import { AppUtil } from '../common/app-utils';

@Component({
  selector: 'app-filtered',
  templateUrl: './filtered.component.html',
  styleUrls: ['./filtered.component.css'],
})
export class FilteredComponent implements OnInit {
  @Input() selectedCategory: string;
  @Output() addedToCart: EventEmitter<any> = new EventEmitter<any>();

  allProducts: any = [];
  resultProductsFiltered: any = [];
  firstClickCategorey: boolean;
  public products$!: Observable<Product[]>;

  constructor(
    private productsService: ProductsService,
    private cartService: CartService
  ) {
    this.selectedCategory = '';
    this.firstClickCategorey = false;
  }

  ngOnInit(): void {
    this.getAll();
  }

  private getAll() {
    this.products$ = this.productsService.getAll();
  }

  addToCart(product: any, quantity: string) {
    this.cartService.addToCart(product, Number(quantity))
    .subscribe(() => {
      window.alert('Your product has been added to the cart!');
      this.addedToCart.emit(Number(quantity));
    })
  }

  public showCategory(category: any): string {
    return AppUtil.getCategory(category);
  }
}
