import { Component, OnInit, ViewChild } from '@angular/core';
import { ProductsService } from './../products.service';
import { CartService } from '../cart.service';
import { CategoriesComponent } from '../categories/categories.component';
import { FilteredComponent } from '../filtered/filtered.component';
import { Observable } from 'rxjs';
import { Product } from '../models/product';

@Component({
  selector: 'app-products',
  templateUrl: './products.component.html',
  styleUrls: ['./products.component.css'],
})
export class ProductsComponent implements OnInit {
  products: any = [];
  recentAdded: number;
  public products$!: Observable<Product[]>;

  @ViewChild(CategoriesComponent) child: CategoriesComponent;
  @ViewChild(FilteredComponent) filteredComponent!: FilteredComponent;

  constructor(
    private productsService: ProductsService,
    private cartService: CartService
  ) {
    this.child = new CategoriesComponent();

    this.recentAdded = 0;
  }

  ngOnInit(): void {
    this.getAll();
  }

  private getAll() {
    this.products$ = this.productsService.getAll();
  }

  addToCart(product: any, quantity: string) {
    this.cartService.addToCart(product, Number(quantity))
    .subscribe(() => {
      window.alert('Your product has been added to the cart!');
    })
  }
  justAdded(addedNumber: number) {
    this.recentAdded = addedNumber;
  }
}
