import { Component, OnInit } from '@angular/core';
import { Observable } from 'rxjs';
import { filter } from 'rxjs/operators';
import { CartService } from '../cart.service';
import { AppUtil } from '../common/app-utils';
import { Product } from '../models/product';
import { AuthenticationService } from '../services/authentication.service';
import { UsersService } from '../users.service';
import { ProductsService } from './../products.service';
@Component({
  selector: 'app-electronics',
  templateUrl: './electronics.component.html',
  styleUrls: ['./electronics.component.css'],
})
export class ElectronicsComponent implements OnInit {
  ProductsOnSale: any = [];
  newProducts1: any = [];
  currentUserName: string = '';
  public products$!: Observable<Product[]>;

  constructor(
    private productsService: ProductsService,
    private cartService: CartService,
    private authenticationService: AuthenticationService
  ) {}

  ngOnInit(): void {
    this.getAll();
    this.authenticationService
      .getCurrentUser()
      .pipe(filter((user) => AppUtil.hasValue(user)))
      .subscribe((user) => {
        this.currentUserName = user.name;
      });
  }

  addToCart(product: any, quantity: string) {
    this.cartService.addToCart(product, Number(quantity)).subscribe(() => {
      window.alert('Your product has been added to the cart!');
    });
  }

  private getAll() {
    this.products$ = this.productsService.getAll();
  }

  getActivatedUser = () => {
    return this.currentUserName;
  };

  public showCategory(category: any): string {
    return AppUtil.getCategory(category);
  }
}
