import { HttpClient } from '@angular/common/http';
import { Injectable, OnInit } from '@angular/core';
import { Observable } from 'rxjs';
import { filter, map } from 'rxjs/operators';
import { AppUtil } from './common/app-utils';
import { AppConsts } from './common/consts';
import { Cart } from './models/cart';
import { CartItem } from './models/cart-item';
import { Product } from './models/product';
import { AuthenticationService } from './services/authentication.service';
@Injectable({
  providedIn: 'root',
})
export class CartService {
  items: CartItem[] = [];
  private readonly url = `${AppConsts.BASE_URL}/api/cart`;
  private readonly addUrl = `${AppConsts.BASE_URL}/api/add-to-cart`;
  cartItems: CartItem[] = [];

  constructor(
    private http: HttpClient,
    private authenticationService: AuthenticationService
  ) {}
  public getCart = (): Observable<Cart> => {
    const userID: string = this.authenticationService.getUserId();
    return this.http.get<Cart>(`${this.url}?user_id=${userID}`);
  };

  public addToCart = (product: Product, quantity: number): Observable<any> => {
    const userID: string = this.authenticationService.getUserId();

    const item = {
      productID: product._id,
      quantity: quantity,
    };

    return this.http.post<any>(`${this.addUrl}?user_id=${userID}`, item);
  };

  // addToCart(product: Product, quantity: number) {
  //   // there is products in the cart we need to check if the item exist in the cart, if yes we need to increase the quantity:
  //   if (this.items.length > 0) {
  //     for (let i = 0; i < this.items.length; i++) {
  //       if (product.serialNumber === this.items[i].product.serialNumber) {
  //         this.items[i].quantity += quantity;
  //         return;
  //       }
  //     }
  //   }
  //   let productToSave: Product = new Product(
  //     product._id,
  //     product.imgUrl,
  //     product.name,
  //     product.price,
  //     product.serialNumber,
  //     AppUtil.getCategory(product.categoryID),
  //     product.description,
  //     product.status
  //   );
  //   // else we add the product as a new one to the cart:
  //   let item: CartItem = new CartItem(quantity, productToSave);
  //   this.items.push(item);
  // }

  getItems(): Observable<CartItem[]> {
    return this.getCart().pipe(
      filter((cart) => AppUtil.hasValue(cart)),
      map((cart) => cart.items)
    );
  }

  clearCart() {
    this.items = [];
    return this.items;
  }

  public delete = (): Observable<any> => {
    const userID: string = this.authenticationService.getUserId();

    return this.http.delete(`${this.url}/delete?user_id=${userID}`);
  };
}
