import { Component, OnInit } from '@angular/core';
import { Observable } from 'rxjs';

import { CartService } from '../cart.service';
import { AppUtil } from '../common/app-utils';
import { CartItem } from '../models/cart-item';
import { Product } from '../models/product';
import { ProductsService } from './../products.service';

@Component({
  selector: 'app-cart',
  templateUrl: './cart.component.html',
  styleUrls: ['./cart.component.css'],
})
export class CartComponent implements OnInit {
  items: CartItem[] = [];
  products: CartItem[] = [];
  cartProductsQuantity!: number;
  constructor(
    private service: ProductsService,
    private cartService: CartService
  ) {}

  ngOnInit(): void {
    this.cartService.getItems().subscribe((cartItems) => {
      this.products = cartItems;
      this.cartProductsQuantity = this.products.length;
    });
  }

  getTotalPrice() {
    let sum = 0;
    for (let item of this.products) {
      // if there is more than one product
      // we multiply the price by the quantity:
      if (item.quantity > 1) {
        sum += item.product.price * item.quantity;
      } else {
        // else we set the price as is:
        sum += item.product.price;
      }
    }

    return sum;
  }

  public showCategory(category: any): string {
    return AppUtil.getCategory(category);
  }

  getProductsQuantity(): number {
    return this.cartProductsQuantity;
  }

  RemoveAll(): void {
    while (this.items.length > 0) {
      this.items.pop();
    }
  }

  public placeOrder() {
    this.cartService.delete().subscribe(() => {});
  }
}
