import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { AppUtil } from '../common/app-utils';
import { AppConsts } from '../common/consts';
import { User } from '../models/user';

@Injectable({
  providedIn: 'root',
})
export class RegistrationService {
  private url: string = `${AppConsts.BASE_URL}/api/register`;
  constructor(private http: HttpClient) {}

  public register(user: User): Observable<any> {
    console.log(`user from reg service ${user.name} ${JSON.stringify(user)}`);
    return this.http
      .post<string>(this.url, user, { observe: 'response' })
      .pipe(catchError(AppUtil.handleError));
  }
}
